.memberreport-page {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: #f4f4f4;
  min-height: 100vh;
  padding: 2rem;
  font-family: 'Segoe UI', sans-serif;
}

.memberreport-card {
  background: #ffffff;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 520px;
  padding: 2rem 2.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

  h3 {
    text-align: center;
    color: #003366;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  h4 {
    color: #3a5f9c;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 1rem;
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 0.3rem;
  }

  .section {
    margin-bottom: 2rem;
  }

  .form-row {
    display: flex;
    align-items: center;
    gap: 1rem;

    label {
      width: 80px;
      color: #333;
      font-weight: 500;
    }

    input[type='date'] {
      flex: 1;
      border: 1px solid #bfbfbf;
      border-radius: 4px;
      padding: 0.4rem 0.6rem;
      background-color: #fafafa;
      font-size: 0.95rem;

      &:focus {
        border-color: #3a5f9c;
        outline: none;
      }
    }
  }

  .actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 1.5rem;

    button {
      border: none;
      border-radius: 4px;
      padding: 0.6rem 1.4rem;
      font-weight: 600;
      cursor: pointer;
      transition: 0.2s ease;
      font-size: 0.95rem;
    }

    .btn-primary {
      background: #3a5f9c;
      color: #fff;

      &:hover {
        background: #2f4f84;
      }
    }

    .btn-secondary {
      background: #dcdcdc;
      color: #333;

      &:hover {
        background: #bfbfbf;
      }
    }
  }
}
