.sanction-page {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: #f4f6f9;
  min-height: 100vh;
  padding: 2rem;
  font-family: 'Segoe UI', sans-serif;
}

.sanction-card {
  background: #fff;
  border: 1px solid #d1d9e6;
  border-radius: 10px;
  width: 750px;
  padding: 2.5rem;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.06);

  h3 {
    text-align: center;
    color: #003366;
    font-weight: 600;
    margin-bottom: 1.5rem;
  }

  h4 {
    color: #3a5f9c;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 1rem;
    border-bottom: 1px solid #e0e0e0;
    padding-bottom: 0.3rem;
  }

  .section {
    margin-bottom: 1.8rem;
  }

  .form-group {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.2rem;
    flex-wrap: wrap;

    label {
      width: 130px;
      color: #333;
      font-weight: 500;
    }

    .input-pair {
      display: flex;
      flex: 1;
      gap: 0.6rem;

      input[type='text'] {
        flex: 1;
        border: 1px solid #bfbfbf;
        border-radius: 4px;
        padding: 0.5rem 0.6rem;
        background-color: #fafafa;
        font-size: 0.95rem;

        &:focus {
          border-color: #3a5f9c;
          outline: none;
        }

        &:first-child {
          width: 100px;
          flex: none;
        }
      }
    }
  }

  .actions {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
    margin-top: 2rem;
    flex-wrap: wrap;

    button {
      border: none;
      border-radius: 4px;
      padding: 0.6rem 1.4rem;
      font-weight: 600;
      cursor: pointer;
      transition: 0.2s ease;
      font-size: 0.95rem;
    }

    .btn-primary {
      background: #3a5f9c;
      color: #fff;
      &:hover {
        background: #2f4f84;
      }
    }

    .btn-secondary {
      background: #dcdcdc;
      color: #333;
      &:hover {
        background: #bfbfbf;
      }
    }

    .btn-outline {
      background: transparent;
      border: 1px solid #3a5f9c;
      color: #3a5f9c;

      &:hover {
        background: #3a5f9c;
        color: #fff;
      }
    }
  }
}
